'use strict';

require('framework_js');

require('pasp_js');

require('pasp_css');

require('testCase_css');

require('jquery_js');

/**
 * 自动页面加载
 */
var dependencies = [];
var pageModules = require.context('./pages/', true, /\.index.js$/);
pageModules.keys().forEach(function (key) {
    dependencies.push(pageModules(key).default);
});

/**
 * 创建应用
 */
var app = angular.module('testCase', dependencies).config(["$httpProvider", function ($httpProvider) {
    //更改 Content-Type
    $httpProvider.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded;charset=utf-8";
    $httpProvider.defaults.headers.post["Accept"] = "*/*";
    $httpProvider.defaults.transformRequest = function (data) {
        //把JSON数据转换成字符串形式
        if (data !== undefined) {
            return $.param(data);
        }
        return data;
    };
}]);

/**
 * 注册应用
 */
angular.bootstrap(document.body, [app.name]);


